.start-fundraising {
  position: relative;
  font-size: var(--font-size-3xl);
  font-weight: 600;
  font-family: var(--font-inter);
  color: var(--color-white);
  text-align: left;
}
.start-fundraising-wrapper {
  cursor: grab;
  border: 0;
  padding: var(--padding-sm);
  background-color: var(--color-seagreen);
  position: absolute;
  top: 461px;
  left: 1282px;
  width: 386px;
  height: 71px;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.imagevideo {
  position: absolute;
  top: 122px;
  left: 84px;
  font-weight: 500;
  display: inline-block;
  width: 260px;
  height: 60px;
}
.line-icon {
  position: absolute;
  top: 673px;
  left: 91px;
  width: 103.04px;
  height: 0;
}
.start-fundraising-container {
  cursor: pointer;
  border: 0;
  padding: var(--padding-sm);
  background-color: var(--color-seagreen);
  position: absolute;
  top: 461px;
  left: 1282px;
  width: 386px;
  height: 71px;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.support-the-elderly {
  margin-block-start: 0;
  margin-block-end: 0;
}
.in-your-community {
  margin: 0;
}
.start-a-fundraising,
.support-the-elderly-container {
  position: absolute;
  display: inline-block;
}
.support-the-elderly-container {
  top: 34px;
  left: 26px;
  font-weight: 600;
  width: 776px;
  height: 180px;
}
.start-a-fundraising {
  top: 214px;
  left: 233px;
  font-size: var(--font-size-xl);
  font-weight: 500;
  color: var(--color-white);
  width: 569px;
  height: 65px;
}
.support-the-elderly-people-in-parent {
  position: absolute;
  top: 122px;
  left: 840px;
  border-radius: var(--br-lg);
  background-color: var(--color-goldenrod-200);
  width: 828px;
  height: 308px;
  overflow: hidden;
  text-align: right;
  font-size: var(--font-size-7xl);
  color: var(--color-snow-200);
}
.frame-group {
  position: absolute;
  top: 261px;
  left: 0;
  background-color: var(--color-gainsboro);
  width: 1728px;
  height: 654px;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-black);
  font-family: var(--font-inter);
}
